body {
  -webkit-text-size-adjust: none;
  text-size-adjust: none; }

input:focus {
  outline: none; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

.right {
  float: right; }

.left {
  float: left; }

.clear {
  clear: both; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.container {
  max-width: 1120px;
  margin: 0 auto; }

.container:after,
.row:after,
.col:after,
.clr:after,
.group:after {
  content: "";
  display: table;
  clear: both; }

.row {
  padding-bottom: 0em;
  margin: 0 -10px; }

.col {
  display: block;
  float: left;
  padding: 0 10px; }

body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; }

