/*sass variable Define here*/
.TopHeader {
  background: #ecebeb;
  width: 100%;
  overflow: hidden; }

.Telephone p {
  color: #333;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  text-transform: capitalize;
  padding: 10px 0px;
  font-weight: 400; }

.Telephone a {
  color: #333;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  text-decoration: none;
  cursor: pointer;
  font-weight: 400; }

.TopMenu {
  float: right; }

.TopMenu ul li {
  display: inline-block;
  color: #333;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  margin: 0px 10px;
  padding: 10px 10px;
  text-transform: uppercase;
  position: relative;
  font-weight: 400; }

.TopMenu ul li:after {
  content: '';
  width: 1px;
  height: 10px;
  background: #333;
  display: block;
  position: absolute;
  top: 50%;
  right: -10px;
  transform: translateY(-50%); }

.TopMenu ul li:last-child:after {
  display: none; }

.TopMenu ul li:last-child {
  margin: 0px 0px 0px 10px;
  padding: 0px 0px 0px 10px; }

.TopSocial ul li {
  display: inline-block;
  width: 30px;
  height: 30px;
  color: #333;
  border: 1px solid #333;
  border-radius: 50%;
  text-align: center;
  margin: 0px 10px;
  position: relative; }

.TopSocial ul li i.fa {
  font-size: 15px;
  text-align: center;
  line-height: 3px;
  display: block;
  padding: 14px 1px;
  border-radius: 50%;
  height: 14px;
  overflow: hidden; }

.Navigation {
  padding: 28px 0px; }

.Navigation ul li a {
  color: #333;
  font-size: 18px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-decoration: none; }

.Navigation ul li {
  display: inline-block;
  margin: 0px 10px;
  padding: 0px 10px; }

.Navigation ul li:first-child {
  margin: 0px 10px 0px 0px;
  padding: 0px 10px 0px 0px; }

.Main {
  float: right;
  padding: 21px 0px; }

.TopSocial {
  float: left; }

.currency {
  float: left; }

.Mainheader {
  padding: 20px 0px; }

.currency {
  position: relative; }

.currency select {
  -webkit-appearance: none;
  width: 80px;
  border-radius: 0px;
  padding: 10px 10px;
  background: #333;
  color: #fff;
  position: relative; }

.currency:after {
  content: '';
  display: block;
  position: absolute;
  top: 12px;
  background: url(./assets/downArrow.png) no-repeat center center/cover;
  width: 12px;
  height: 12px;
  right: 6px; }

.item {
  position: relative; }

.homeSlider img {
  width: 100%;
  display: block; }

.ImageContent {
  position: absolute;
  top: 43%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%); }

.InnerContent {
  text-align: center;
  width: 300px;
  height: 300px;
  border: 1px solid #fff;
  padding: 30px; }

.InnerContent h5 {
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  padding: 0px 10px;
  line-height: 17px; }

.InnerContent h4 {
  color: #333;
  font-size: 22px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  text-align: center;
  margin-top: 25px; }

.InnerContent h3 {
  color: #333;
  font-size: 70px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  text-align: center; }

.Upto {
  position: relative;
  text-align: center; }

.Upto h5:after {
  content: '';
  display: block;
  width: 50px;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 5px;
  right: 28px; }

.Upto h5:before {
  content: '';
  display: block;
  width: 50px;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 5px;
  left: 28px; }

.Upto h5 span:after {
  content: '';
  display: block;
  width: 30px;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 15px;
  right: 48px; }

.Upto h5 span:before {
  content: '';
  display: block;
  width: 30px;
  height: 1px;
  background: #fff;
  position: absolute;
  bottom: 15px;
  left: 48px; }

.ImageContent a {
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  padding: 10px 30px;
  background: #e9a061;
  display: block;
  width: 200px;
  margin: 40px auto 0; }

ul.slick-dots {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%); }

ul.slick-dots li {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: #ccc;
  margin: 0px 10px;
  transform: rotate(45deg); }

ul.slick-dots li.slick-active {
  background: #e9a061; }

ul.slick-dots li button {
  display: none; }

.Footer {
  background: #333;
  padding: 30px 0px; }

.list h3 {
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: 30px; }

.list ul li {
  color: #fff;
  font-size: 14px;
  text-transform: capitalize;
  font-family: "Poppins", sans-serif;
  margin: 10px 0px;
  font-weight: 400; }

.Connected {
  float: right;
  width: 70%; }

.Connected h3 {
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-bottom: 30px; }

.Connected h3 span {
  font-style: italic;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: lowercase; }

.Connected ul li {
  display: inline-block;
  width: 38px;
  height: 38px;
  color: #333;
  border: 1px solid #333;
  border-radius: 50%;
  text-align: center;
  margin: 0px 10px;
  position: relative; }

.Connected ul li i.fa {
  font-size: 15px;
  text-align: center;
  line-height: 11px;
  display: block;
  padding: 14px 1px;
  border-radius: 50%;
  height: 37px;
  overflow: hidden;
  background: #fff; }

.Connected ul {
  margin-bottom: 30px; }

.newsInput {
  position: relative; }

.newsInput input.email {
  -webkit-appearance: none;
  border: 1px solid #ccc;
  width: 100%;
  height: 40px;
  padding: 2px 10px;
  background: transparent;
  color: #fff; }

.newsInput input.subbtn {
  position: absolute;
  top: 50%;
  right: 4px;
  -webkit-appearance: none;
  height: 33px;
  background: #666;
  color: #fff;
  transform: translateY(-50%);
  border: none;
  width: 28%; }
